#shipping_method .woo-bg--econt-delivery{
  min-width:260px; }
  #shipping_method .woo-bg--econt-delivery li.multiselect__element{
    display:block !important; }
  #shipping_method .woo-bg--econt-delivery .multiselect__option.multiselect__option--highlight:after,
  #shipping_method .woo-bg--econt-delivery .multiselect__option--highlight:not(.multiselect__option--selected){
    background:#234182; }
  #shipping_method .woo-bg--econt-delivery .multiselect__option.multiselect__option--highlight.multiselect__option--selected:after,
  #shipping_method .woo-bg--econt-delivery .multiselect__option--highlight.multiselect__option--selected{
    background:#d63638; }
  #shipping_method .woo-bg--econt-delivery .multiselect__content-wrapper ul.multiselect__content{
    margin:0;
    padding:0; }
  #shipping_method .woo-bg--econt-delivery .woo-bg-multiselect{
    margin-bottom:15px; }
  #shipping_method .woo-bg--econt-delivery .woo-bg-multiselect input.multiselect__input{
    border:0; }
  #shipping_method .woo-bg--econt-delivery .multiselect__select{
    height:100%; }
  #shipping_method .woo-bg--econt-delivery .woo-bg-multiselect--additional-field{
    width:100%; }
  #shipping_method .woo-bg--econt-delivery button.mfp-close{
    background-color:initial !important; }
